const Precio = (props) => {
    console.log(props);
    const {price, percent_change_1h, percent_change_7d, percent_change_24h, volume_24h} = props.precio
    return ( 
        <div className="card text-white bg-success mb-3">
            <div className="card-header">Precio del Bitcoin</div>
            <div className="card-body">
                <h4 className="card-title">Precio Actual: $ {price}</h4>
                <div className="d-md-flex justify-content-between">
                    <p className="card-text">
                        <span className="font-weight-bold">Variacion Ultima Hora: {percent_change_1h}%</span>
                    </p>
                    <p className="card-text">
                        <span className="font-weight-bold">Variacion Ultimas 24 Hora: {percent_change_24h}%</span>
                    </p>
                    <p className="card-text">
                        <span className="font-weight-bold">Variacion Ultima Semana: {percent_change_7d}%</span>
                    </p>
                    <p className="card-text">
                        <span className="font-weight-bold">Volumen Ultimas 24 Hora: {volume_24h}</span>
                    </p>
                </div>
            </div>
        </div>
    );
}
 
export default Precio;